.items{
    display: flex;
    flex-direction: row-reverse;
    border:2px solid red;
    width:20rem;
    margin: 10px 0 10px 0;
    padding-left: 4px;
}

#item{
    display: flex;
    flex-direction: row;
    margin: 10px 0 10px 0;
}

#price,#quant{
    display: flex;
    font-size: 1.3rem;
    margin: 5px 0 5px 4px;
}
#quant button{
    width: 120px;
    height: 25px;
    font-weight: 700;
    font-size: 1rem;
    text-align: center;
    align-items: center;
    cursor: pointer;
    color: black;
    outline: none;
    border: 2px solid rgb(171, 167, 167);
    background-color: rgba(219, 199, 199, 0.9);
}
#img{
    width: 100px;
    height: 120px;
}
#name{
    font-size: 1.5rem;
    margin-left: 4px;
    margin-top: 5px;
}
#oprice{
    text-decoration: line-through;
    font-size: 1.25rem;
    margin-top: 7px;
}
#dprice{
    color:rgb(245, 9, 9);
    margin: 0 6px 0 4px;
    font-size: 1.5rem;
}
#discount{
    color: green;
    margin-right: 5px;
}
#crossButton{
    background: none;
    border: none;
}
#crossButton:hover{
    cursor: pointer;
}
#crossImg{
    width : 10px;
    height : 10px;
}